<template>
  <!--标题开始-->
  <section class="wrap">
    <header>
      <div class="mianTitle">活动汇总表</div>
    </header>
    <!--标题结束-->
    <section class="contentSamplist content">
        <div class="companyName">{{companyName}}</div>
      <div id="bg" class="statusSample">
        <ul class="showPartCustomer">
          <li class="clearfix" v-for="(activity,index) in Activities">
            <a href="javascript:;" class="fl" @click=" SampleDetailList(index)">
              <div class="clearfix">
                   <span class="fl">
                     <img src="../../../../../static/img/company/wechat/activitysheet/dateIcon.png" alt="" class="userIcon">
                   </span>
                <div class="fl">
                  <div class="customerPartMsg">
                    <span>{{activity.activity_time}}</span>
                    <span>报告人数:{{activity.sampling_number}}人</span>
                  </div>
                  <div class="customerMsgId">
                    <span>项目 : {{activity.projects}}</span>
                    <span>样本状态 : {{activity.sample_status}}</span>
                  </div>
                  <div class="customerPartMsg"><span>{{activity.address}}</span></div>
                </div>

                <div v-if="activity.feedback_state=='已核对'" class="fr isActCheckedBtn">已核对</div>
                <div v-else class="fr isActCheckedBtn2" @click.stop="comfirmActivity($event,activity.activity_id)">活动结束</div>
              </div>
            </a>
            <div class="fr">
              <!--<a href="javascript:;" class="isChecked">{{activity.feedback_state}}</a>-->
              <!--<a href="javascript:;" class="toDetail" @click="SampleDetailList(index)">详情</a>-->
            </div>
          </li>
        </ul>
      </div>
    </section>
    <!--标题结束-->
    <footerpart></footerpart>
  </section>
</template>

<script>
  import headerpart from "../headerpart"
  import footerpart from "../footerpart"
  import axios from 'axios'
  import {mapState,mapMutations} from 'vuex'
  export default {
    data(){
      return {
          companyName:'',
          Activities:[{
              sampling_number: 0,
              address: '',
              activity_id: '',
              projects: '',
              sample_status:'',
              feedback_state:'',
              activity_time: '',
              receiver: null
          }]

      }
    },
    mounted:function(){
        this.GetSampleListData();
        this.companyName=this.companyInfo.companyName;
    },
    components: {
      headerpart,
      footerpart
    },
    computed:{
       ...mapState([
       'companyInfo',
       ]),
      },
      methods: {
          ...mapMutations([
              'RECORD_ACTIVITIES_ID'
          ]),
          comfirmActivity(event,id){
              var vm = this;

              axios.post('/wechat/sample/comfirmActivity',{params:{_id:id}})
                  .then(function (response) {
                      if(response.data.result=='00'){
                          vm.$messagebox({
                              title:"提示",
                              message:"操作成功"
                          });
                          event.srcElement.className = "fr isActCheckedBtn";
                          event.srcElement.innerHTML = "已核对";
                      }

                  })
          },
          GotoSampleCheck(index){
              this.RECORD_ACTIVITIES_ID(this.Activities[index].activity_id)
              this.$router.push('/company/samplecheck')
          },
          SampleDetailList(index){
              this.RECORD_ACTIVITIES_ID(this.Activities[index].activity_id)
              this.$router.push('/company/sampledetaillist')

          },
          GetSampleListData(){
              let vm=this;
              axios.get('/wechat/sample/samplelist')
                  .then(function (response) {
                      console.log("response：")
                      console.log(response.data)

                      if(response.data !=null) {
                          vm.Activities.splice(0, vm.Activities.length)

                          for (let i = 0; i < response.data.length; i++) {

                              vm.Activities.push(response.data[i]);

                          }
                      }
                  })
                  .catch(function (response) {
                      console.log("error response：")
                      console.log(response);
                  })
          }
      }
  }
</script>
<style>
  .content{
    margin-top:2.22rem;
  }
  input::placeholder{
    text-align: left;
  }
  #bg{
    background:#fff;
  }
  .statusSample{
    height:25rem;
    overflow-y: auto;
  }
  /*.contentSamplist{
    padding:0 0.4rem;
  }*/
  .isChecked{
    color:#ed3a36;
    /*line-height: 3rem;*/
  }
  .isChecked2{
    color:#48ab00;
    /*line-height: 3rem;*/
  }
  .isChecked img{
    width:1rem;
    height:1rem;
  }
  .toDetail{
    background: #338fff;
    text-align: center;
    line-height: 1rem;
    border-radius: 0.2rem;
    margin-top:0.8rem;
    color: #fff;
    display: block;
    height: 1rem;
    width:2rem;
  }
  .searchCustomerMsg{
    margin-top:0.5rem;
    height:1rem;
    width:1rem;
  }
  .searchContentBox input{
    margin:0.5rem;
    border:1px solid #ccc;
  }
  .userIcon{
    display: block;
    margin:0.5rem 0 0 0.5rem;
    width:0.9rem;
    height:0.9rem;
  }
  .customerPartMsg span{
    margin:0.5rem 0.5rem;
  }
  .customerMsgId{
    padding:0.3rem 0 0.3rem 1rem;
  }
  .customerMsgId span{
    padding-right:1rem;
  }
  .showMoreMsg img{
    display: block;
    width:1rem;
    height:1rem;
    margin:0 auto;
  }
  .showPartCustomer li{
   /* width:100%;
    height: 100%;*/
    color:#333;
    padding-top:0.5rem;
    border-bottom:1px solid #dcdcdc;
    box-shadow: 0rem 0.1rem 0.5rem #ccc ;
  }
  .showPartCustomer li a{
    width: 100%;
  }
  .isActCheckedBtn{
    height: 1.2rem;
    line-height: 1.2rem;
    width: 2.5rem;
    text-align: center;
    background: #cccccc;
    color:#fff;
    margin:1rem 1rem 0 1rem;
  }
  .isActCheckedBtn2{
    height: 1.2rem;
    line-height: 1.2rem;
    width: 2.5rem;
    text-align: center;
    background: #338fff;
    color:#fff;
    margin:1rem 1rem 0 1rem;
  }
</style>



